import React, { useEffect, useState } from 'react';
import Start from './pages/Start';
import { ThemeProvider } from '@material-ui/styles';
import theme from './styles/mat-theme';
import svgPattern from './bgPattern.svg';
import { 
  BrowserRouter as Router, Route, Switch
} from 'react-router-dom';
import './App.scss';
import UserContext from './context/userContext';
import { Clubhouse } from './pages/clubhouse';
import userService, { getLatestUser } from './services/user/userServices';
import TeamSelect from './pages/teamselect';
import useFetch from './hooks/useFetch';

function App() {
  const [ user, setUser ] = useState(null);
  const [ twitterUserInfo, setTwitterUserInfo ] = useState(null);
  const [ latestUsers, latestUsersLoading, latestUsersError, runLatestUser ] 
    = useFetch(getLatestUser(), "users", false);

  useEffect(() => {
    (async() => {
      const userInfo = await userService.getUserInfo();

      if (!userInfo) return;
      const usersList = await userService.getUsersFromTwitterId(parseInt(userInfo.userId));

      if (usersList.length === 0) {
        setTwitterUserInfo(userInfo);
        runLatestUser(true);
      } else {
        setUser(usersList[0]);
      }
    })();
  }, [runLatestUser]);

  useEffect(() => {
    (async() => {
      if (!latestUsersLoading && latestUsers && latestUsers[0].userId) {
        const userInfo = twitterUserInfo;
        userInfo.ffoUserId = latestUsers[0].userId + 1;
        const userResult = await userService.createUser(userInfo);
        userResult.error && userResult.error.message ? 
            console.error('unable to create user: ', userResult.error.message) :
            setUser(userResult.record);
      } else if (latestUsersError) {
        console.error('unable to create user without latest user', latestUsersError);
      }
    })();
  }, [latestUsers, latestUsersLoading, latestUsersError, twitterUserInfo])
  
  const base = process.env.NODE_ENV === "production" ? "/ffo" : "/";
  
  return user &&
    <UserContext.Provider value={user}>
      <div className="app">
        <div className="background">
          <img alt="background pattern" src={svgPattern}></img>
        </div>
          <ThemeProvider theme={theme}>
            <Router basename={base}>
              <Switch>
                <Route exact path='/'>
                  <Start />
                </Route>
                <Route exact path='/new'>
                  <TeamSelect />
                </Route>
                <Route exact path='/clubhouse/:teamId'>
                  <Clubhouse />
                </Route>
                <Route path='*'>
                  <Start />
                </Route>
              </Switch>
            </Router>
          </ThemeProvider>
      </div>
    </UserContext.Provider>;
}

export default App;
